class URLObject 
{   public url:any;
    public bufferViewOffset:any;
    public bufferViewLength = 0;

    public bufferIndex:any;
    public bufferViewIndex:any;
    public accessorIndex:any;
    public meshIndex:any;
    public nodeIndex:any;
    constructor(_url:any)
    {
        this.url = _url;
        this.bufferViewOffset = 0;
        this.bufferViewLength = 0;

        this.bufferIndex = null;
        this.bufferViewIndex = null;
        this.accessorIndex = null;
        this.meshIndex = null;
        this.nodeIndex = null;
   
    }

}


class GltfUrlExtractor
{
    private glftJSON:any;
    constructor(_glftJSON:any) {
        this.glftJSON = _glftJSON;
    }

    getNodeURLs(nodeIndex:number){
        let urlObjArray = [];
        if(nodeIndex !== null && nodeIndex !== undefined)
        {
            let nodes = this.glftJSON.nodes;
            if(nodeIndex < nodes.length)
            {
                let meshIndex = nodes[nodeIndex].mesh;
                urlObjArray = this.getMeshURLs(meshIndex);  
                urlObjArray.forEach(item => {
                    item.nodeIndex = nodeIndex;
                });
            }
        }
        return urlObjArray;

    }

    getMeshURLs(meshIndex:any){
        let urlObjArray:any[] = [];
        if(meshIndex !== null && meshIndex !== undefined)
        {
            let meshes = this.glftJSON.meshes;
            if(meshIndex < meshes.length){
                let mesh = meshes[meshIndex];

                let accesstorIndices:any[] = [];  
                mesh.primitives.forEach((primitive:any) => {
                    accesstorIndices.push(primitive.attributes.POSITION);
                    accesstorIndices.push(primitive.indices);
                });  
                
                accesstorIndices.forEach(accessorIndex => {
                    let urlObj = this.getAccessorURLs(accessorIndex)
                    if(urlObj){
                        urlObj.meshIndex = meshIndex;    
                        urlObjArray.push(urlObj);
                    }
                        
                });
               
            }
      
        }
        return urlObjArray;
    }

    getAccessorURLs(accessorIndex:any){
        let urlObj = null;
        if(accessorIndex !== null && accessorIndex !== undefined){
            let accessors = this.glftJSON.accessors;
            if(accessorIndex < accessors.length){
                let bufferViewIndex = accessors[accessorIndex].bufferView;
                urlObj =  this.getBufferViewURLs(bufferViewIndex);       
                urlObj.accessorIndex = accessorIndex;       
            }            
        }
        return urlObj;

    }

    getBufferViewURLs(bufferViewIndex:any){
        let urlObj = null;
        if(bufferViewIndex !== null && bufferViewIndex !== undefined){
            let bufferViews = this.glftJSON.bufferViews;
            if(bufferViewIndex < bufferViews.length){
                let bufferIndex = bufferViews[bufferViewIndex].buffer;
                let offset = bufferViews[bufferViewIndex].byteOffset;
                let length = bufferViews[bufferViewIndex].byteLength;
                let url = this.getBufferURLs(bufferIndex);  
                if(url)
                {
                    urlObj = new URLObject(url)  
                    urlObj.bufferViewOffset = offset || 0;
                    urlObj.bufferViewLength = length || 0;
                    urlObj.bufferViewIndex = bufferViewIndex;
                    urlObj.bufferIndex = bufferIndex;
                }  
                

            }                 
               
        }
        return urlObj;
    }

    getBufferURLs(bufferIndex:any){
        let uri = null;
        if(bufferIndex !== null && bufferIndex !== undefined){
            let buffers = this.glftJSON.buffers;
            if(bufferIndex < buffers.length){
                let buffer = buffers[bufferIndex];
                uri = buffer.uri;                
            }            
        }
        return uri;
    }   

}


export { GltfUrlExtractor };