import { Logger as logger } from '../Logger.js';
import * as glmatrix from "gl-matrix";
import { probeMode } from '../AppContants.js';
import { AppState } from '../AppState.js';
class GUIState{
    public planeOptions:PlaneGUIState[];
    constructor(){
        this.planeOptions = [
            new PlaneGUIState(),
            new PlaneGUIState(),
            new PlaneGUIState()
        ]
    }
}

export enum SelectionMode{
    NONE = 0,
    THREE_PT = 1,
    FACE = 2
}

class PlaneGUIState{
    public selectionMode:SelectionMode
    public isPlaneEnabled:boolean;
    public isPlaneVisible:boolean;
    public isSlicePlaneEnabled:boolean;
    public isSlicePlaneVisible:boolean;
    public prevRot:[number,number];
    public prevTrans: [number,number];
    public sliderMinMax: [number,number];
    public sliceSliderMinMax: [number,number];
    public primarySliderValue:number;
    public sliceSliderValue:number;
    public rotSliderUValue:number;
    public rotSliderVValue:number;
    public rotSliderNValue:number;
    public delta:number;
    public deltaSlice:number;
    public deltaRotU:number;
    public deltaRotV:number;
    public deltaRotN:number;
    constructor(){
        this.selectionMode = SelectionMode.NONE;
        this.isPlaneEnabled = false;
        this.isPlaneVisible = false;
        this.isSlicePlaneEnabled = false;
        this.isSlicePlaneVisible = false;
        this.prevRot = [0,0];
        this.prevTrans = [0,0];
        this.sliderMinMax = [0,0];
        this.sliceSliderMinMax = [0,0];
        this.primarySliderValue = 0;
        this.sliceSliderValue = 0;
        this.rotSliderUValue = 180;
        this.rotSliderVValue = 180;
        this.rotSliderNValue = 180;
        this.delta = 0;
        this.deltaSlice = 0;
        this.deltaRotU = 0;
        this.deltaRotV = 0;
        this.deltaRotN = 0;
    }
}
class Section
{
    private renderApp:any;
    private appState:AppState;
    private activePlaneId:number;
    private events:any;
    private externalEventDispatcher:any;
    private bbox:any;
    private slicePlaneOffset:[number,number,number];
    private guiState:GUIState;
    private selectedPts:any[];
    private selectionMode:SelectionMode;
    constructor(_renderApp:any, appState:AppState)
    {
        this.renderApp = _renderApp;
        this.appState = appState;
        this.activePlaneId = null;
        this.events = this.renderApp.getEvents();
        this.externalEventDispatcher = this.renderApp.getEventDispatcher();
        this.registerEvents();
        this.bbox = _renderApp.getSceneBoundingBox();
        let offset = this.bbox.getRadius()/4;
        this.slicePlaneOffset = [offset,offset,offset];
        this.guiState = new GUIState();
        this.selectedPts = [];
        this.selectionMode = SelectionMode.NONE;
        this.setBounds(0);
        this.setBounds(1);
        this.setBounds(2);
    }

    registerEvents(){
        this.externalEventDispatcher.addEventListener(this.events.PROBE_FINISH,this.handleSelection.bind(this));
    }

    handle3ptSelect(probeData:any){
        if(this.selectedPts.length > 0 &&  this.selectedPts[this.selectedPts.length -1] == probeData.nearPoint)
        {
            return;
        }
        else{
            this.selectedPts.push(probeData.hitPoint);
        }
        if(this.selectedPts.length == 3)
        {
            this.planeFrom3pts(this.activePlaneId,this.selectedPts[0],this.selectedPts[1],this.selectedPts[2]);
            this.selectedPts = [];
        }
        logger.setStatusBar("Probing from 3pt section");
    }
    handleFaceSelect(probeData:any){

        if(probeData.primitiveSize == 3)
        {
            const data = probeData.primitiveData;
            this.selectedPts = [
                [data[0],data[1],data[2]],
                [data[3],data[4],data[5]],
                [data[6],data[7],data[8]]
            ];
            this.planeFrom3pts(this.activePlaneId,this.selectedPts[0],this.selectedPts[1],this.selectedPts[2]);
            this.selectedPts = [];
            logger.setStatusBar("Probing from face section");
        }
        else{
            logger.setStatusBar("Please select a face");
        }
        
    }
    handleSelection(e:any){
        let probeData = e.data;

        if(probeData.hitPoint && this.appState.probeMode == probeMode.SECTION)
        {
            if(this.selectionMode == SelectionMode.THREE_PT)
            this.handle3ptSelect(probeData);
            else if(this.selectionMode == SelectionMode.FACE)
            this.handleFaceSelect(probeData);
        }
    }

    clearPlaneState(data:PlaneGUIState){
        data = new PlaneGUIState();
    }

    resetPlane(index:number){
        this.clearPlaneState(this.guiState.planeOptions[index])
        this.setBounds(index);
    }
    getSectionGUIData(){
        return this.guiState;
    }

    setSectionGUIData(guiState:GUIState){
        this.guiState = guiState;
    }
    
    setSectionPlaneEquation(planeId:number, eqn:[number,number,number,number]){
        this.renderApp.setSectionPlaneEquation(planeId,eqn);
    }
    
    getSectionPlaneEquation(planeId:number) {
        return this.renderApp.getSectionPlaneEquation(planeId);
    }
    setSelection(planeId:number, mode:SelectionMode){
        this.appState.probeMode = probeMode.SECTION;
        this.activePlaneId = planeId;
        this.selectionMode = mode;
    }
    setPlaneState(planeId:number, params:any){

        this.setSelection(planeId,params.selectionMode);

        if(params.isPlaneEnabled)
        {
            this.renderApp.enableClipPlane(planeId)
        }
        else{
            this.renderApp.disableClipPlane(planeId)
        }
        if(params.isSlicePlaneEnabled)
        {
            this.renderApp.enableSlicePlane(planeId)
        }
        else{
            this.renderApp.disableSlicePlane(planeId)
        }
        if(params.isPlaneVisible)
        {
            this.renderApp.showClipPlane(planeId)
        }
        else{
            this.renderApp.hideClipPlane(planeId)
        }
        if(params.isSlicePlaneVisible)
        {
            this.renderApp.showSlicePlane(planeId)
        }
        else{
            this.renderApp.hideSlicePlane(planeId)
        }
        this.guiState.planeOptions[planeId] = params;
        this.translatePlane(params.delta,
                            params.deltaSlice,planeId);
        this.rotatePlane(params.deltaRotU,
                        params.deltaRotV,
                        params.deltaRotN,
                        planeId);
    }
    invert(planeId:number){
        let currentEqn = this.getSectionPlaneEquation(planeId);
        if(currentEqn == undefined)
        return;
        const invNormal = [-currentEqn[0],-currentEqn[1],-currentEqn[2]];
        this.setSectionPlaneEquation(planeId,[...invNormal ,-currentEqn[3]]as [number,number,number,number]);
    }
    planeFrom3pts(planeId:number,p1:glmatrix.vec3,p2:glmatrix.vec3,p3:glmatrix.vec3){
        const kEdge1 = glmatrix.vec3.create();
        const kEdge2 = glmatrix.vec3.create(); 
        glmatrix.vec3.sub(kEdge1,p2,p1)
        glmatrix.vec3.sub(kEdge2,p3,p1)
		const n = glmatrix.vec3.create();
		glmatrix.vec3.cross(n,kEdge1,kEdge2);
        glmatrix.vec3.normalize(n,n);
        let d = -glmatrix.vec3.dot(n,p1);
        if(glmatrix.vec3.sqrLen(n) > 0.0001){
            const eqn = [n[0],n[1],n[2] ,d];
            this.setSectionPlaneEquation(planeId,eqn as [number,number,number,number]);
            console.log('plane eqn from 3pts',eqn);
        }
    }
    translatePlane(delta:number,deltaSlice:number,planeId:number){
        if(delta == 0 && deltaSlice == 0)
        return;
        this.slicePlaneOffset[planeId] +=deltaSlice;
        this.renderApp.translateSectionPlane(delta,deltaSlice,planeId);
        // let currentEqn = this.getSectionPlaneEquation(planeId);
        // if(currentEqn == undefined)
        // return;
        // currentEqn[3] += delta;
        // this.renderApp.setSlicePlaneOffset(this.slicePlaneOffset[planeId],planeId);
        // this.setSectionPlaneEquation(planeId,currentEqn)

    }
    rotatePlane(deltaU:number,deltaV:number,deltaN:number,planeId:number){
        if(deltaU == 0 && deltaV == 0 && deltaN == 0)
        return;
        this.renderApp.rotateSectionPlane(deltaU,deltaV,deltaN,planeId);
    }
    setBounds(index:number){
        this.guiState.planeOptions[index].sliderMinMax = [-this.bbox.getRadius(),this.bbox.getRadius()];
        this.guiState.planeOptions[index].sliceSliderMinMax = [0,this.bbox.getRadius()];
        this.guiState.planeOptions[index].sliceSliderValue = this.slicePlaneOffset[index];
    }
    resetSection(){
        this.resetPlane(0);
        this.resetPlane(1);
        this.resetPlane(2);
        this.renderApp.resetSection();
    }


}
export { Section };