import * as appDefaults from './AppDefaults.js';
import * as appContants from './AppContants.js';
import {vec3} from "gl-matrix";

class Legend
{
    private id:any;
    private orientation:any;
    private paletteType:any;
    private valueType:any;
    private valuePlacement:any;
    private ticsPosition:any;
    private colorValueMode:any;
    private colorMap:any;
    private isUserCustomDefinedValue:any;
    private userDefinedValues:any[];
    private min:any;
    private max:any;
    constructor(id:any){
        this.id = id;
        this.orientation = appDefaults.Default.LEGEND_ORIENTATION;
        this.paletteType = appDefaults.Default.LEGEND_PALETTE_TYPE;
        this.valueType = appDefaults.Default.LEGEND_VALUE_TYPE;
        this.valuePlacement = appDefaults.Default.LEGEND_VALUE_PLACEMENT;
        this.ticsPosition = appDefaults.Default.LEGEND_TICS_POSITION;
        this.colorValueMode = appDefaults.Default.LEGEND_COLOR_VALUE_MODE;
        
        this.colorMap = appDefaults.Default.LEGEND_COLOR_MAP;
        this.isUserCustomDefinedValue = false;
        this.userDefinedValues = [];
        this.min = null;
        this.max = null;
    }

    setMinMAX(min:any,max:any){
        this.min = min;
        this.max = max;
    }

    getDisplayData(){
        var range = this.getRange();
        let data = {
            colors :  this.colorMap,
            paletteType : this.paletteType,
            range : range,           
        }
        return data;
    }

    getNewColorSet(newSize:number,inColorArray:any[]){ 
        let outColorArray:any[] = []

            if(inColorArray.length < 2)
                return outColorArray;
            if(newSize<=0)
                return outColorArray;
        let iOldColorSize=inColorArray.length;//size of input
            if(newSize == iOldColorSize)
            {
                outColorArray = [...inColorArray];
                return outColorArray;
            }
            if(newSize==1)
            {
                if(inColorArray.length%2==0)
                {
                    let idxStart = inColorArray.length/2-1;
                    let idxEnd = idxStart+1;
                    let color = vec3.create();
                    color[0] = (inColorArray[idxStart][0]+inColorArray[idxStart+1][0])*0.5;
                    color[1] = (inColorArray[idxStart][1]+inColorArray[idxStart+1][1])*0.5;
                    color[2] = (inColorArray[idxStart][2]+inColorArray[idxStart+1][2])*0.5;
                    outColorArray.push(color);
                }
                else
                {
                    let idx = inColorArray.length*0.5;
                    outColorArray.push(inColorArray[idx]);
                }	
                return outColorArray;
            }
            let GapRatio = (iOldColorSize-1)/(newSize-1)  ;//count for each size of input
            //set first color as it is
            outColorArray.push(inColorArray[0]);
            let position=0.0;
            for(let i=1;i<newSize;i++)
            {
                position +=GapRatio;
                let intPart= parseInt(position.toString());
                let fracPart=position-intPart;
                let color1 = inColorArray[intPart];
                let color2 = vec3.create();
                if(intPart<iOldColorSize-1)
                color2=inColorArray[intPart+1];
                else
                color2=vec3.fromValues(0,0,0);

                let rIncr = (color2[0] - color1[0]) * fracPart;
                let gIncr = (color2[1] - color1[1]) * fracPart;
                let bIncr = (color2[2] - color1[2]) * fracPart;

                let color = vec3.create();
                color[0]=color1[0]+rIncr;
                color[1]=color1[1]+gIncr;
                color[2]=color1[2]+bIncr;
                outColorArray.push(color);
            }
            return outColorArray;
    }

    createTexture(colorArray:any[], textureSize:any, isDiscrete:boolean){
        let out = [];
        if(isDiscrete){
          const band = Math.floor(textureSize / colorArray.length);
          const delta = textureSize - band*colorArray.length;
          for( let x=0; x<textureSize; x++)
          {
              const fract = x* colorArray.length / textureSize;
                      const i = Math.floor(fract);
                      const r = colorArray[i][0];
                      const g = colorArray[i][1];
                      const b = colorArray[i][2];
                      out.push(colorArray[i]);
                  }
        }
        else{
          out = this.getNewColorSet(textureSize,colorArray);
        }
        return out;
    }

    getTextureData(){
        let input = [...this.colorMap];
        let isDiscrete = this.paletteType == appContants.paletteType.DISCRETE;
        let texture256Array = this.createTexture(input.reverse(),256,isDiscrete);
        let textureData:any[] = [];
        texture256Array.forEach((color)=>{
          textureData.push(...color);
          //textureData.push(255);
        })
        return textureData;
    }

    getRange(){
        let range = [];
        let colorCount = this.colorMap.length;

              
        let step = (this.max - this.min) / colorCount;
        if(this.paletteType ===  appContants.paletteType.CONTINUOUS) {       
            step = (this.max - this.min) / (colorCount - 1);
            colorCount = colorCount - 1;
        }
  
        for(let i = 0; i < colorCount + 1; i++)
        {
            let val = this.min + (step * i);
            range.push(val);

        }

        return range.reverse();
    }

}
export { Legend };