import {RepresentationType} from "./MCAXConstants";
class MCAXFilter {
    private renderApp:any;
    private mcax:any;
    constructor(renderer:any, mcax:any){
        this.renderApp = renderer;
        this.mcax = mcax;
    }
    getRenderableGeometries()
    {
        let models = this.mcax.models;
        if(models instanceof Array && models.length > 0)
        {   
            let filteredModels = models.filter(item => item.display);  
            let componentIndexList = filteredModels.map(item => (item.components || [])).flat();             
           
            let components = this.getRecursiveItems(componentIndexList, this.mcax.components); 
            let filteredcomponents = components.filter(e => e.item.display);          
            let geometryIndexList = filteredcomponents.map(e => (e.item.geometries || [])).flat(); 

            let geometries = this.getRecursiveItems(geometryIndexList,this.mcax.geometries);

            return geometries;      
        }
    }    

    getRecursiveItems(indexList:any[], items:any[])
    {
        let itemsList:any[] = [];
        if(indexList instanceof Array && indexList.length > 0){
            indexList.forEach( index => {
                let item = items[index];                
                if(item.children && item.children.length > 0)
                {
                    itemsList.push(...this.getRecursiveItems(item.children, items))
                }
                else{
                  
                    itemsList.push({
                        item, 
                        index 
                    });
                }                
            })
        }
        return itemsList;
    }
    getVisibleGeometries(representationsId:any){
        let geometries = this.getRenderableGeometries();
        let visibleGeometries:any[] = [];
        representationsId.forEach((id:any,index:any) => {
            for (let i = 0; i < geometries.length; i++) {
                const geo = geometries[i].item;
                if(geo.representations && geo.representations.length > 0 && geo.representations.includes(id)){
                    if(visibleGeometries[index]){
                        visibleGeometries[index].name = geo.name;
                        visibleGeometries[index].representations.push(id);
                    }
                    else{
                        visibleGeometries[index] = {
                            name: geo.name,
                            representations: [id]
                        }
                    }
                    break;
                }   
            }
        });
        return visibleGeometries;
    }
    getRepresentationType(id:any){
        let curr_rep = this.mcax.representations[id];
        if(curr_rep)
        return RepresentationType[curr_rep.type];
    }
}

export {MCAXFilter}