//import '@ungap/global-this';
import { version } from './Version.js';
import { Viewer } from './Viewer';
import { Utility } from './Utility.js';
import { AppConnector } from './AppConnector.js';
import { Logger as logger } from './Logger.js';
import { EventDispatcher } from './EventDispatcher.js';
import { viewerEvents, globalEvents } from './Events.js';

/*
import debug from 'debug';
const log = debug('app:log');
// The logger should only be disabled if we’re not in production.
if ( globalThis && globalThis.process.env.NODE_ENV.toString() !== 'production') {
    debug.enable('*');
    log('Logging is enabled!');
  } else {
    debug.disable();
  }
*/
  
class ViewerManager {
    private viewerMap : Map<any,any>;
    private connector : AppConnector;
    private defaultViewerID : any;
    private eventDispacther:EventDispatcher;
    constructor() {
        this.viewerMap = new Map(); 
        this.eventDispacther = new EventDispatcher();  
        this.connector = new AppConnector(this.eventDispacther);
        this.defaultViewerID = null;        
    }     

    //#region General
    getVersion(){
        return version;
    }

    createViewer(_containerID:any){
        let viewerUUID = Utility.create_UUID();
        let viewer = new Viewer(viewerUUID, _containerID, this.connector, this.eventDispacther );     
        if(this.defaultViewerID === null){
            this.defaultViewerID = viewerUUID;
        }
        this.viewerMap.set(viewerUUID, viewer);
        return viewerUUID;      
    }

    getDefaultViewerID(){
        return this.defaultViewerID;
    }

    getEventsList(){
        return { viewerEvents, globalEvents };
    }

    getEventDispatcher(){
        if(this.eventDispacther){
            return this.eventDispacther;
        }
        return null;   
    }   

    deleteViewer(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            viewer.clear();
            return viewer.delete();   
        }
        return "Invalid viewer ID";    
    } 
       
    loadModel(api:string, url:string, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.loadModel(api, url);  
        }
        return Promise.reject("Invalid viewer ID");    
    }   

    getModelInfo(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.getModelInfo();
        }
        return "Invalid viewer ID";   
    }

    showModel(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.showModel(); 
        }
        return Promise.reject("Invalid viewer ID");
    }

    getViewer(viewerUUID:any):Viewer{
        return viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID)
    }

    setExternalLogger(externalLogger:any)
    {
        logger.setExternalLogger(externalLogger);
    }
    //#endregion

    //#region part and product tree
    getProductTree(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer)
            return viewer.getProductTree();                  
        return "Invalid viewer ID";
    }

    setSelectedParts(selectedParts:any[], viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.setSelectedParts(selectedParts);
        }
        return "Invalid viewer ID";
    }

    async setPartsVisibility(selectedParts:string[],visibility:boolean,viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return await viewer.setPartsVisibility(selectedParts,visibility);
        }
        return Promise.reject("Invalid viewer ID");
    }

    async invertPartsVisibility(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return await viewer.invertPartsVisibility();
        }
        return Promise.reject("Invalid viewer ID");
    }

    getDisplayModes(selectedNodes:any[], viewerUUID:any ){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.getDisplayModes(selectedNodes);
        }
        return Promise.reject("Invalid viewer ID");
    }
    
    async setDisplayMode(displayModeId:string,selectedNodes:any[],viewerUUID:any){
        let viewer:Viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return await viewer.setDisplayMode(displayModeId,selectedNodes);
        }
        return Promise.reject("Invalid viewer ID");
    }

    async getSearchHints(viewerUUID:any ){
        let viewer:Viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.getSearchHints();
        }
        return Promise.reject("Invalid viewer ID");
    }

    setHighlightedNodes(selectedNodes:any[],toHighlight:boolean,viewerUUID:any){
        let viewer:Viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.setHighlightedNodes(selectedNodes,toHighlight);
        }
    }
    // changeDisplayedNodes(selectionInfo:any, viewerUUID:any){
    //     let viewer = this.getViewer(viewerUUID);
    //     return viewer.changeDisplayedNodes(selectionInfo);
    // }
    //#endregion

    //#region CAE Result
    isCAEResultAvailable(viewerUUID:any):boolean{
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getIsCAEResultAvailable();
    }

    getDisplayResult(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getDisplayResult();
    }

    getResults(viewerUUID:any){  
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getResults();
    }

    getSteps(selectedResultIndex:number, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);    
        return viewer.getSteps(selectedResultIndex);         
    }

    getDerivedTypes(selectedResultIndex:number, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);   
        return viewer.getDerivedTypes(selectedResultIndex);        
    }

    applyResult(resultId:string, stepId:string, derivedTypeId:string, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);      
        return viewer.applyResult(resultId, stepId, derivedTypeId);
    }

    getLegendData(viewerUUID:any)
    {
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);      
        return viewer.getLegendData();
    }
    //#endregion
    
    //#region Probing
    probeFromNodes(pickInfo:any,viewerUUID:any)
    {
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);  
        viewer.probeFromNodes(pickInfo);
    }
    //#endregion
    
    //#region Section
    createSection(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.createSection();
    }

    getSectionGUIData(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getSectionGUIData();
    }

    setSectionPlaneEquation({planeId, eqn}:any,viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        viewer.setSectionPlaneEquation(planeId,eqn);
    }
    
    getSectionPlaneEquation(planeId:number, viewerUUID:any) {
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getSectionPlaneEquation(planeId);
    }
    
    setPlaneState(planeOptions:any, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        viewer.setPlaneState(planeOptions);
    }

    invertSectionPlane(planeId:number,viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        viewer.invertSectionPlane(planeId);
    }

    planeFrom3pts(planeId:number,p1:number[],p2:number[],p3:number[],viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        viewer.planeFrom3pts(planeId,p1,p2,p3);
    }

    resetSection(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        viewer.resetSection();
    }
    //#endregion

    //#region PartManipulation
    enablePickAndMove(toEnable:boolean,viewerUUID:any) {
        let viewer:Viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.enablePickAndMove(toEnable);
        }
    }
    //#endregion
    //#region Quick tools
    fitView(selectedNodes:any[]=[],viewerUUID:any):any{
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer)       
            return viewer.fitView(selectedNodes);
        return "Invalid viewer id";
      
    }

    captureScreen(viewerUUID:any):any{
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer)
            return viewer.captureScreen();
        return "Invalid viewer id";
        
    }
    //#endregion
};

export default (new ViewerManager);
