import  { serverURLs, errorCode, ServerConnectionType } from './connector/Contants.js';
import  { ServerConnector } from './connector/ServerConnector.js';
import { Logger as logger, statusIconType } from './Logger.js';
import { viewerEvents, getEventObject } from './Events.js';

class AppConnector {
  
    private connector:ServerConnector;
    private eventDispacther:any;
    private baseUrl:any;
    constructor (_eventDispacther:any) {
        this.connector = new ServerConnector(ServerConnectionType.AXIOS)
        this.eventDispacther = _eventDispacther;     
    }

    getArrayBuffer ( url:any ){
        return this.connector.getArrayBuffer(url);        
    }

    getJsonData ( url:any ){
        return this.connector.getJsonData(url);        
    }   
    
    getNetworkMetrics(){
        return this.connector.getNetworkMetrics();
    }

    getModel(API:any, caxFilePath:any, viewerID:any){
        //console.log("Downloading file on the server.");
        logger.setStatusBar("Checking the file on the server.");
        let obj = getEventObject(viewerEvents.MODEL_DOWNLOAD_STATUS_UPDATE, viewerID, "Checking the file on the server");
        this.eventDispacther.dispatchEvent(obj);
        
        let scope = this;
        let url = API + "?url=" + caxFilePath;
        var returnPromise = new Promise(function(resolve, reject) {
            scope.getJsonData(url)
            .then((jsonResult:any) => {
              if (scope.isJson(jsonResult) === true && jsonResult) {
                if (jsonResult["json_url"]) {
                  if (jsonResult["data"]) {
                    let status = jsonResult["data"]["status"];
                    let taskId = jsonResult["data"]["task_id"];
                    let taskURL = jsonResult["data"]["task_url"];
                    if (status === "available") {  
                      logger.clearStatusBar();                    
                      resolve(jsonResult);
                    } else if (status === "download_in_progress") {
                      let statusMessage = "File download in progress";    
                      logger.setStatusBar("Downloading file on the server");   
                      let obj = getEventObject(viewerEvents.MODEL_DOWNLOAD_STATUS_UPDATE, viewerID, "Downloading file on the server");
                      scope.eventDispacther.dispatchEvent(obj);        
                      scope.checkTaskStatus(taskURL, taskId, viewerID , statusMessage, () =>{
                        logger.clearStatusBar();     
                        resolve(jsonResult)
                      });                     
                    }
                  }
                } else if (jsonResult["error"]) {
                  //console.log(jsonResult["error"]);
                  logger.setStatusBar(jsonResult["error"],statusIconType.ERROR);  
                  reject(jsonResult["error"]);
                } else {
                  //console.log(errorCode.jsonError);
                  logger.setStatusBar(errorCode.jsonError,statusIconType.ERROR);  
                  reject(errorCode.jsonError);
                }
              } else {
                //console.log(errorCode.jsonError);
                logger.setStatusBar(errorCode.jsonError,statusIconType.ERROR);  
                reject(errorCode.jsonError);
              }
            })
            .catch(errorMsgCode => {
              if (errorMsgCode !== errorCode.noerror) {
                //console.log(errorMsgCode);
                logger.setStatusBar(errorMsgCode,statusIconType.ERROR);  
                reject(errorMsgCode);
              } else {
                //console.log(errorCode.unknownError);
                logger.setStatusBar(errorCode.unknownError,statusIconType.ERROR);  
                reject(errorCode.unknownError);
              }
            });
        });
        return returnPromise;
    }

    checkTaskStatus(taskURL:any,taskId:any, viewerID:any, statusMessage:any,onCompleteCallbackFn:any) 
    {
      let scope = this;
      if ((taskId || taskURL) && onCompleteCallbackFn) { 
        
        let url = this.baseUrl + serverURLs["taskState"] + "/" + taskId;
        if (taskURL) url = taskURL;  

        this.getJsonData(url)
          .then((jsonResult:any) => {
            if (this.isJson(jsonResult) === true && jsonResult) {
              if (jsonResult["state"] === "SUCCESS") {
                onCompleteCallbackFn(jsonResult);
              } else {
                let msg = statusMessage;
                if (
                  jsonResult["info"] &&
                  jsonResult["info"]["bytes_downloaded"] &&
                  jsonResult["info"]["total_bytes"]
                ) {
                  let bytes_downloaded = jsonResult["info"]["bytes_downloaded"];
                  let total_bytes = jsonResult["info"]["total_bytes"];
                  let percetage = (bytes_downloaded / total_bytes) * 100;
                  msg = statusMessage + " : " + percetage.toFixed(1) + "% completed.";
                }
                logger.setStatusBar(msg);      
                let obj = getEventObject(viewerEvents.MODEL_DOWNLOAD_STATUS_UPDATE, viewerID, msg);
                scope.eventDispacther.dispatchEvent(obj);       
                //console.log(msg);
  
                setTimeout(
                  function() {
                    this.checkTaskStatus(
                      taskURL,
                      taskId,
                      viewerID,
                      statusMessage,
                      onCompleteCallbackFn
                    );
                  }.bind(
                    this,
                    taskURL,
                    taskId,
                    viewerID,
                    statusMessage,
                    onCompleteCallbackFn
                  ),
                  3000
                );
              }
            } else {
              //console.log(errorCode.jsonError);
              logger.setStatusBar(errorCode.jsonError,statusIconType.ERROR);         
            }
          })
          .catch(errorMsgCode => {
            if (errorMsgCode !== errorCode.noerror) {
              //console.log(errorMsgCode);
              logger.setStatusBar(errorCode.jsonError,statusIconType.ERROR);    
            } else {
              //console.log(errorCode.unknownError);
              logger.setStatusBar(errorCode.jsonError,statusIconType.ERROR);    
            }
          });
      } else {
          //console.log("checkTaskStatus :: Incorrect number of arguments.");
          logger.setStatusBar(errorCode.jsonError,statusIconType.ERROR);    
      }
    }

    isJson(item:any) {
        item = typeof item !== "string" ? JSON.stringify(item) : item;

        try {
            item = JSON.parse(item);
        } catch (e) {
            return false;
        }

        if (typeof item === "object" && item !== null) {
            return true;
        }

        return false;
    }
    
}


export { AppConnector };

