import { ResponseType, errorCode } from './Contants.js';
import  NetworkMetrics  from './NetworkMetrics.js';
import  axios from 'axios';

class AxiosConnector{

    constructor () {       
    }

    static send(_url:any, _data:any, _method:any, _reponseType:any) {
        if (!_url) throw new Error('Invalid request parameters');

        let responseType = _reponseType || ResponseType.JSON;
        if (responseType === ResponseType.JSON || responseType === ResponseType.BUFFER)
        {
            return new Promise(function(resolve, reject) {
                let requestObject = NetworkMetrics.addURL(_url);
                requestObject.requestInitaited();
                axios({
                    method: _method,
                    url: _url,
                    data: _data,
                    responseType: responseType,
                    onDownloadProgress: function(event: any) {
                        requestObject.addProgress(event)
                     }
                })
                .then(function (response : any) {
                    if(response){
                        requestObject.succeeded();
                        resolve(response.data);
                    }                     
                    else{
                        requestObject.errorOccurred(errorCode.invalidDataError);
                        reject(errorCode.invalidDataError);
                    }
                       
                  })
                .catch(function (error : any) {
                    let errorMsgCode = errorCode.noerror;
                    if (error.response) {
                        if (error.response.status === 0) {
                            errorMsgCode = errorCode.connectionError;
                        } else if (error.response.status === 404) {
                            errorMsgCode = errorCode.error404;
                        } else if (error.response.status === 500) {
                            errorMsgCode = errorCode.serverError;
                        } else {
                            errorMsgCode = errorCode.unknownError;
                        }  
                      } else if (error.request) {
                        errorMsgCode = errorCode.noresponseError;
                      } else {
                        errorMsgCode = errorCode.unknownError;                   
                      }
                    requestObject.errorOccurred(errorMsgCode);
                    reject(errorMsgCode);
                  })
            });
        }           
        else throw new Error('Invalid Response Type');
    }
}

export { AxiosConnector };