export enum viewerEvents {
    MODEL_DOWNLOAD_STATUS_UPDATE = "MODEL_DOWNLOAD_STATUS_UPDATE",
    MODEL_PART_HIGHLIGHTED = "MODEL_PART_HIGHLIGHTED"
}

export enum globalEvents {
    ERROR = "ERROR",
    INFO = "INFO",
    WARN = "WARN",
    LOG = "LOG"
}

export const getEventObject = function(type:any, viewerID:any, data:any){
    return {
        type,
        viewerID,
        data
    }
}