import { AppConnector } from './AppConnector.js';
import { GltfUrlExtractor } from './GltfUrlExtractor.js';
import { Logger as logger, statusIconType } from './Logger.js';
import { Utility } from './Utility.js';

class ProgressiveLoader
{
    private mcax:any;
    private renderApp:any;
    private connector:AppConnector;
    private gltfurlExtractor:GltfUrlExtractor;
    constructor(_mcax:any, _renderApp:any, _connector:AppConnector) {
        this.mcax = _mcax;
        this.renderApp = _renderApp;
        this.connector = _connector;
     
        this.gltfurlExtractor = new GltfUrlExtractor(_mcax.gltf);    
    }

    //########################################################

    async showDefaultDisplay(){
        if(this.mcax &&  this.renderApp)
        {
            logger.setStatusBar("Downloading mesh data...");
            let representations:any[] = [];
            let geometries  = this.getRenderableGeometries();
            geometries.forEach( (e, gIndex) => {
            let repre = this.getRecursiveItems(e.item.representations, this.mcax.representations); 
            repre.forEach((r,rIndex) =>{
                    if(r.item.display === true){
                        let repIndex = r.index[rIndex];
                        let repObj = {
                            customData: r.item,
                            geometryIndex:e.index[gIndex],
                            id: "representation_" + repIndex,
                            name: r.item.name
        
                        }  
                        representations.push(repObj);
                    }

                });
            }); 

            this.loadSelectedRepresentations(representations)
            .then(()=>{
                logger.setStatusBar("Mesh data downloaded.");
                this.renderApp.fitView(); 
                return(true);
            }).catch((error => {
                logger.setStatusBar("Error occurred while loading mesh.", statusIconType.ERROR);
                throw new Error(error);
            })).finally(()=>{                
            });            
        }
        else{
            throw new Error("Invalid mcax or renderApp object.");
        }    
    }

    getRenderableGeometries()
    {
        let models = this.mcax.models;
        if(models instanceof Array && models.length > 0)
        {   
            let filteredModels = models.filter(item => item.display);  
            let componentIndexList = filteredModels.map(item => (item.components || [])).flat();             
           
            let components = this.getRecursiveItems(componentIndexList, this.mcax.components); 
            let filteredcomponents = components.filter(e => e.item.display);          
            let geometryIndexList = filteredcomponents.map(e => (e.item.geometries || [])).flat(); 

            let geometries = this.getRecursiveItems(geometryIndexList,this.mcax.geometries);

            return geometries;      
        }
    }    

    getRecursiveItems(indexList:any[], items:any[])
    {
        let itemsList:any[] = [];
        if(indexList instanceof Array && indexList.length > 0){
            indexList.forEach( index => {
                let item = items[index];                
                if(item.children && item.children.length > 0)
                {
                    itemsList.push(...this.getRecursiveItems(item.children, items))
                }
                else{
                  
                    itemsList.push({
                        item, 
                        index 
                    });
                }                
            })
        }
        return itemsList;
    }

    //########################################################

    loadSelectedRepresentations(representations:any[]){
        return new Promise((resolve, reject) => {
            if(representations && representations instanceof Array)
            {        
                var rep_map = new Map(); 
                representations.forEach(rep => {
                    let level  = rep.customData.level;
                    if(!(rep_map.get(level) instanceof Array))                
                        rep_map.set(level, []);                  
                    (rep_map.get(level)).push(rep);                
                });
                
                let levelKeys = [ ...rep_map.keys() ];
                let sortedLevels = levelKeys.sort((a, b) => (a-b));
                let promises:any[] = [];
                logger.setStatusBar("Downloading data...");
                sortedLevels.forEach( level => {
                    promises.push(this.loadLevelNodes(rep_map.get(level), level, sortedLevels));
                });         

                Promise.all(promises).finally(() => {
                    logger.setStatusBar("Data downloaded.");
                    resolve(true);
                }); 
            }
            else{
                //console.log("Invalid representations");
                reject("Invalid representations")
            }
        });
    }

    getRepresentationsByLevel(representationList:any[], level:any){
        let scope = this;
        let representations:any[] = [];
        representationList.forEach(element => {
            let geometry = scope.mcax.geometries[element.geometryIndex];
            geometry .representations.forEach((repIndex:any) => {
                if(scope.mcax.representations[repIndex].level == level)  {
                    let repObj = {
                        customData: scope.mcax.representations[repIndex],
                        geometryIndex: element.geometryIndex,
                        id: "representation_" + repIndex,
                        name: scope.mcax.representations[repIndex].name

                    }              
                    representations.push(repObj);
                }                
            });                  
        });
        return representations;

    }

    async loadLevelNodes(representationList:any[], currentLevel:any, allselectedLevels:any){
        return new Promise(async (resolve) => {
            if(representationList instanceof Array){        
                let downloadRepreList:any[] = [];
                representationList.forEach(element => {   
                    //let gltfNodeIndex =  element.node;
                    //if(element.customData)
                    let gltfNodeIndex =  element.customData.node;
                    if(this.renderApp.getIsRenderDataAvailable([gltfNodeIndex]))            
                        this.renderApp.setNodeVisibility([gltfNodeIndex], true);               
                    else 
                        downloadRepreList.push(element);        
                
                });  

                if(downloadRepreList.length > 0){
                    let lod = this.mcax.lods.filter((item:any) => (item.level === currentLevel));  
                    if(lod.length === 0){
                        let nodeList = downloadRepreList.map(item => item.customData.node);
                        this.renderApp.setNodeVisibility(nodeList, true); 

                        await this.loadRespresentationNodes(downloadRepreList);    
                        //this.renderApp.fitView();                    
                        resolve(true);
                    }                                 
                    else{
                        let progression  = lod[0].progression;
                        for(let i=0; i < progression.length; i++)
                        {
                            let level = progression[i];
                            let representations = this.getRepresentationsByLevel(downloadRepreList, level);
                            if(level !== currentLevel)
                                await this.loadLevelNodes(representations, level, allselectedLevels);
                            else  {
                                let nodeList = representations.map(item => item.customData.node);
                                this.renderApp.setNodeVisibility(nodeList, true); 
                                await this.loadRespresentationNodes(representations);  
                            }                      
                                                          

                            if( i !== 0){    
                                if(!(allselectedLevels.includes(progression[i - 1])))
                                {                        
                                    let representations = this.getRepresentationsByLevel(representationList, progression[i - 1]); 
                                    let nodeList = representations.map(item => item.customData.node);
                                    this.renderApp.setNodeVisibility(nodeList, false); 
                                }
                            }         
                            //else
                                //this.renderApp.fitView();
                        
                        }
                        resolve(true);

                    }
                }  
                else{
                    //this.renderApp.fitView();     
                    resolve(true);
                }
            }
        });
    }   

    async loadRespresentationNodes(representationList:any[]){
        let urlObjArray:any[] = [];
        representationList.forEach(element => {   
            //let gltfNodeIndex =  element.node;
            //if(element.customData)
            let gltfNodeIndex =  element.customData.node;
            let urlOjb = this.gltfurlExtractor.getNodeURLs(gltfNodeIndex);    
            urlObjArray.push(urlOjb);    
        });

        let clubbedURLObjList = ManagedURLDownloader.processURLObject(urlObjArray);
        let maxLength = 0;
        let urlCount = 0;
        clubbedURLObjList.forEach(item =>{
            maxLength = (item.length > maxLength) ? item.length : maxLength;
            urlCount += item.length;
        })

        let counter = 0;
        return new Promise((resolve) => {    
            for(let i = 0; i < maxLength; i++ )
            {
                for(let j = 0; j < clubbedURLObjList.length; j++ ){
                    if(i < clubbedURLObjList[j].length){
                        let URLobj = clubbedURLObjList[j][i];
                        //console.log(URLobj);                    
                        this.connector.getArrayBuffer(URLobj.url)
                        .then((arrayBuffer:any) => {
                            this.splitandUpdateBuffer(URLobj,arrayBuffer);
                            counter += 1;

                            if(counter === urlCount){
                                resolve(true);
                            }                              
                        });
                    }   
                }
            }

        });
    

        /*
        for (let i=0;i < clubbedURLObjList.length; i++)
        {
            for(let j=0;j < clubbedURLObjList[i].length; j++)
            {
                let URLobj = clubbedURLObjList[i][j];
                //console.log(URLobj);                    
                this.connector.getArrayBuffer(URLobj.url)
                .then((arrayBuffer) => {
                    this.splitandUpdateBuffer(URLobj,arrayBuffer)
                });                    
            }
        }
        */
      

    }
      
    splitandUpdateBuffer(URLobj:any, arrayBuffer:any[]){
        let chunkstring = Utility.getURLParameterByName(URLobj.url, "chunks");
        let chunks = chunkstring.split(",");
        let offset = 0;
        chunks.forEach( (chunk , index) => {
            let length = parseInt(chunk.split('+')[1]);
            this.renderApp.replaceWEBGLBuffer(URLobj.nodes[index], arrayBuffer.slice( offset, offset + length ));
            offset =  offset + length;
        });        
    }

    loadLevelNodes_working(representationList:any[]){
        if(representationList instanceof Array){
            let urlObjArray:any[] = [];
            representationList.forEach(element => {   
               let gltfNodeIndex =  element.customData.node;
               if(this.renderApp.getIsRenderDataAvailable([gltfNodeIndex]))               
                    this.renderApp.setNodeVisibility([gltfNodeIndex], true);               
               else{           
                    let urlOjb = this.gltfurlExtractor .getNodeURLs(gltfNodeIndex);    
                    urlObjArray.push(urlOjb);              
               }
            });                        
            let clubbedURLObjList = ManagedURLDownloader.processURLObject(urlObjArray);

            let maxLength = 0;
            clubbedURLObjList.forEach(item =>{
                maxLength = (item.length > maxLength) ? item.length : maxLength;
            })

            for(let i = 0; i < maxLength; i++ )
            {
                for(let j = 0; j < clubbedURLObjList.length; j++ ){
                    if(i < clubbedURLObjList[j].length){
                        let URLobj = clubbedURLObjList[j][i];
                        //console.log(URLobj);                    
                        this.connector.getArrayBuffer(URLobj.url)
                        .then((arrayBuffer:any) => {
                            this.splitandUpdateBuffer(URLobj,arrayBuffer)
                        });
                    }   
                }
            }

            /*
            for (let i=0;i < clubbedURLObjList.length; i++)
            {
                for(let j=0;j < clubbedURLObjList[i].length; j++)
                {
                    let URLobj = clubbedURLObjList[i][j];
                    //console.log(URLobj);                    
                    this.connector.getArrayBuffer(URLobj.url)
                    .then((arrayBuffer) => {
                        this.splitandUpdateBuffer(URLobj,arrayBuffer)
                    });                    
                }
            }
            */

        }  
    } 
}

class ManagedURLDownloader
{

    static downloadSizeLimit = 4 * 1024 * 1024; // 4 MB
    constructor(){       
    }

    static processURLObject(urlObjArray:any[])
    {
        let urlMap = new Map();
        urlObjArray.forEach( item => {
            item.forEach((urlObj:any) => {
                if(!(urlMap.get(urlObj.url)))
                    urlMap.set(urlObj.url, []);
                (urlMap.get(urlObj.url)).push(urlObj);
            });
        });

        let uniqueURLs = [ ...urlMap.keys() ];
        let clubbedURLObjList:any[] = [];
        uniqueURLs.forEach( url => {
            clubbedURLObjList.push(this.URLMerger(url, urlMap.get(url)));
        });  
       return clubbedURLObjList;
 
    }

    static URLMerger(_url:any, specificUrlObjArray:any[])
    {
        let groupedIndex = [];
        let clubbedSize = 0;
        let tempIndex:any[] =[];
        for(let i=0; i<specificUrlObjArray.length; i++)
        {
            let size = specificUrlObjArray[i].bufferViewLength;
            if(size >= ManagedURLDownloader.downloadSizeLimit)      {      
                groupedIndex.push(i);            
            }
            else{
                if((clubbedSize + size) >= ManagedURLDownloader.downloadSizeLimit)
                {      
                    tempIndex = [i];
                    clubbedSize = size;
                    groupedIndex.push(tempIndex);
                }
                else{                  
                  if(tempIndex.length === 0)
                       groupedIndex.push(tempIndex);
                    
                  tempIndex.push(i); 
                  clubbedSize += size;
                }                    
            }
        }

        //console.log(groupedIndex);
        let clubbedURLObjects:any[] = [];
        groupedIndex.forEach(item =>{
            let queryString= "";
            let nodes = []   
            if(item instanceof Array) {

                let indexedItems = item.map( (value,index) => { return {index,  value} });
                let sortedItems = indexedItems.sort((x, y) => { return (( specificUrlObjArray[x.value].bufferViewOffset > specificUrlObjArray[y.value].bufferViewOffset)  ? 1 : ((specificUrlObjArray[x.value].bufferViewOffset === specificUrlObjArray[y.value].bufferViewOffset)  ? 0 : -1))});
                let sortedIndex = sortedItems.map(function(e){return e.value});

                sortedIndex.forEach( i => {
                    let offset = specificUrlObjArray[i].bufferViewOffset || 0;
                    let length = specificUrlObjArray[i].bufferViewLength || 0;
                    if(queryString === "" )                    
                        queryString = "chunks=" + offset + "%2B" +  length;     //%2B = +                 
                    else
                        queryString += "," + offset + "%2B" +  length; //%2B = + 
                    nodes.push(specificUrlObjArray[i]);
                });
            }
            else{               
                let offset = specificUrlObjArray[item].bufferViewOffset || 0;
                let length = specificUrlObjArray[item].bufferViewLength || 0;
                queryString = "chunks=" + offset + "%2B" +  length;       //%2B = +        
                nodes.push(specificUrlObjArray[item]);
            }

            let bufferURI = _url;

            let url = ( bufferURI.includes('?') ?
                        bufferURI + "&" + queryString :
                        bufferURI + "?" + queryString );

            

            let objects = {
                url : url,
                nodes : nodes
            }
            clubbedURLObjects.push(objects);

        });

        return clubbedURLObjects;

    }

}

export { ProgressiveLoader };