    constructor(_mcax:any, _renderApp:any, _connector:AppConnector, _legendManager:LegendManager)
    {
        this.mcax = _mcax;
        this.renderApp = _renderApp;
        this.connector = _connector;
        this.legendManager = _legendManager;
        this.legendID = null;
        this.result = null;
        if(this.getIsCAEResultAvailable())
        this.createResult();
  

        this.appliedResultId = null;
        this.appliedStepId = null;
        this.appliedDerivedTypeId = null;

        if(this.mcax.selection)
        {  
            if( this.mcax.selection.variableIndex != undefined &&  this.mcax.selection.variableIndex != null)
                this.appliedResultId = this.mcax.selection.variableIndex;
            if( this.mcax.selection.stepIndex != undefined &&  this.mcax.selection.stepIndex != null)
                this.appliedStepId =  this.mcax.selection.stepIndex;
            if( this.mcax.selection.derivedTypeIndex != undefined &&  this.mcax.selection.derivedTypeIndex != null)
                this.appliedDerivedTypeId =  this.mcax.selection.derivedTypeIndex;   
                
            if(this.legendManager)
                this.legendID = this.legendManager.createLegend();
        }  
    }

    getIsCAEResultAvailable():boolean{
        let variables = this.mcax.variables;
        if(variables){
            let activeVariables = variables.filter((item:any) => item.active);
            if(activeVariables.length > 0)
                return true;
        }
        return false;
    }

    private setDefaultDerived(variableTypeId:string, defaultValue:number) {
        this.result.variableTypes[variableTypeId].defaultDerived = this.mcax.derivedTypes[defaultValue].generator;
    }
    private createResult(){
        this.result = <ICAEResult>{
            variables: {},
            derivedTypes: {},
            missingVariableSteps: {},
            variableTypes: {},
            stepVariables: {},
            selection: {}
        }
        try {

            this.result.selection = <ISelection>{
                    variableId: this.mcax.variables[this.mcax.selection.variableIndex].id,
                    stepId: this.mcax.steps[this.mcax.selection.stepIndex].id,
                    derivedTypeId: this.mcax.derivedTypes[this.mcax.selection.derivedTypeIndex].generator
            }

            this.mcax.variableTypes.forEach((variableType:any) => {
                if(variableType.active == true){
                    this.result.variableTypes[variableType.id] = <IVariableType>{
                        name: variableType.name,
                        defaultDerived: "",
                        derivedTypeIds: variableType.derivedTypes.map((e:number) =>this.mcax.derivedTypes[e].generator)
                    }
                }
            })
            this.mcax.variables.forEach((variable:any) => {
                if(variable.active == true) {
                    this.result.variables[variable.id] = <IVariable>{
                        name: variable.name,
                        type: this.mcax.variableTypes[variable.type].id,
                        attributes: {}
                    }
                    this.setDefaultDerived(this.mcax.variableTypes[variable.type].id,variable.defaultDerived['colorplot'])
                }
            });
            this.mcax.derivedTypes.forEach((derivedType:any) => {
                this.result.derivedTypes[derivedType.generator] = <IDerivedType>{
                    name: derivedType.name,
                    generator: derivedType.generator
                }
            })
            this.mcax.steps.forEach((step:any) => {
                if(step.active == true) {
                    this.result.stepVariables[step.id] = <IStepVariable>{
                        name: step.name
                    }
                }

            })
            //missing steps
            this.mcax.variables.forEach((variable:any) => {
                if(this.mcax.steps.length !== variable.stepsAvailable.length){
                    let missingStepIds:IMissingVariableStep[] = [];
                    this.mcax.steps.forEach((step:any,index:number) => {
                        if(!variable.stepsAvailable.find((sIndex:number) => index == sIndex)){
                            missingStepIds.push(step.id);
                        }
                    });
                    this.result.missingVariableSteps[variable.id] = missingStepIds;
                }
                else{
                    this.result.missingVariableSteps[variable.id] = [];
                }
            });

            console.log(this.result);

        }
        catch(error:any) {
            throw new Error(error);
            
        }
    }
