import { Utility } from '../Utility.js';
import {IPartDisplayState} from "./DisplayTypes";
import { displayModes } from '../AppContants';

interface TreeNode {
    id:string,
    pid: string | null,
    children: string[],
    customData: any,
    title: string,
    attributes: any
}

class ModelTree {
    models: Map<string,TreeNode>
    rootNodeIds: string[]

    constructor(treeMap:Map<string,TreeNode>, modelIds:string[]){
        this.models = treeMap;
        this.rootNodeIds = modelIds;
    }
    getVisibleNodeIds():string[] {
        if(this.models) {
            let visible = [...this.models.values()].filter((node:TreeNode) => (node.children.length === 0 && node.customData.displayProps.visibility == true))
            return visible.map(node => node.id);
        }
    }
    getInvisibleNodeIds():string[] {
        if(this.models) {
            let invisible = [...this.models.values()].filter((node:TreeNode) => (node.children.length === 0 && node.customData.displayProps.visibility == false))
            return invisible.map(node => node.id);
        }
    }
    getPartNodeFromNodeIds(nodeIds:string[]): TreeNode[]{
        let out:TreeNode[] = [];
        if(this.models){
            nodeIds.forEach((nodeId:string) => {
                const selectedNode = this.models.get(nodeId);
                if(selectedNode)
                out.push(selectedNode);
            })
        }
        //console.log(out);
        return out;
    }

    getAllPartNodes():TreeNode[] {
        if(this.models) {
            return [...this.models.values()].filter((node:TreeNode) => node.children.length === 0)
        }
    }

    getRepresentationsFromParts(nodes:TreeNode[]): any[] {
        let out:any[] = [];
        nodes.forEach((part:TreeNode) => {
            if(part.customData && part.customData.geometries)
            {
                part.customData.geometries.forEach((geometry:any) => {
                    if(geometry.customData && geometry.customData.representations)
                    {
                        out.push(...geometry.customData.representations)
                    }
                });
            }
        })
        return out;
    }

    getRenderNodeIdsFromNodeIds(nodeIds:string[]){
        let nodes = this.getPartNodeFromNodeIds(nodeIds);
        let reps = this.getRepresentationsFromParts(nodes);
        return reps.map((rep) => rep.customData.node);
    }

    getPartIdsFromRenderNodeIds(renderIds:number[]){
        let partIds:string[] = [];
        [...this.models.values()].forEach((node:TreeNode) => {
            if(node.children.length === 0){
                if(node.customData && node.customData.geometries)
                {
                    node.customData.geometries.forEach((geometry:any) => {
                        if(geometry.customData && geometry.customData.representations)
                        {
                            geometry.customData.representations.forEach((rep:any) => {
                                if(renderIds.includes(rep.customData.node))
                                {
                                    partIds.push(node.id);
                                }
                            })
                        }
                    });
                }
            } 
        })
        return partIds;
    }

    setVisibility(nodes:TreeNode[],toShow:boolean) {
        nodes.forEach(node => {
            let curNode = {...this.models.get(node.id)};
            curNode.customData.displayProps.visibility = toShow;
            this.models.set(curNode.id,curNode);
        })
    }
}

class ModelTreeBuilder {
    private mcax:any;
    private tree:Map<string,TreeNode>;
    constructor(_mcax:any){
        this.mcax = _mcax;     
        this.tree = new Map<string,TreeNode>();
    }

    build(): ModelTree{       
        let rootNodeIds = this.processModels(Utility.deepCopy(this.mcax.models));
        return new ModelTree(this.tree,rootNodeIds);    
    }

    private processModels(models:any):string[]{
        let modelIds:string[] = [];
        if(models && models instanceof Array && models.length > 0)
        {          
            let filteredModels = models.filter(this.filter);  

            for(let j = 0 ; j < filteredModels.length; j++ )
            {
                let model = filteredModels[j];
                let children:string[] = [];
                if(model.components){ 
                    let filteredComponents = model.components.filter((item:any) => this.filter( this.mcax.components[item]));
                    for ( let i = 0, l = filteredComponents.length; i < l; i ++ ) {               
                        let childID = this.buildComponentsHierachy("model_"+ j, filteredComponents[ i ] , this.mcax.components);
                        children.push(childID);                   
                    }                     
                }

                let name = model.name || "model_" + j;   
                let tempModel:TreeNode = {
                    //name,
                    id : "model_" + j,
                    pid : null,
                    children,
                    customData : model,
                    title : name,
                    attributes : model.attributes
                };

                this.tree.set(tempModel.id,tempModel);
                modelIds.push(tempModel.id);
            }

        }
        return modelIds;
    }

    private buildComponentsHierachy(pid:any, componentId:any, allComponents:any) {

        let component = JSON.parse(JSON.stringify(this.mcax.components[ componentId ]));
        let children:string[] = [];

        if(component.geometries){
            //children = this.processGeometries(component.geometries);
            component.geometries = this.processGeometries(component.geometries);
            component.displayProps = <IPartDisplayState>{
                displayId: displayModes.DM_1.ID,
                hiddenlineEnabled:false,
                transparency:0.0,
                useTexture:false,
                isHighlighted: false,
                visibility:component.display
            }
        }       
        if ( component.children ) {
            let filteredChildren = component.children.filter((item:any) => this.filter(allComponents[item])); 
            for ( let i = 0, l = filteredChildren.length; i < l; i ++ ) {
                let compo:string = this.buildComponentsHierachy("component_"+componentId, filteredChildren[ i ] , allComponents);
                children.push(compo);   
            }

        }

        let name = component.name || "component_" + componentId;    
        let tempComponent:TreeNode = {
            //name,
            id : "component_" + componentId,
            pid,
            children,
            customData : component,
            title : name,
            attributes : component.attributes
        };
        this.tree.set(tempComponent.id,tempComponent);
        return tempComponent.id;       
    }
    
    private buildChildHierachy(pid:any, childId:any, allmodels:any , allComponents:any) {

        let model = JSON.parse(JSON.stringify(allmodels[ childId ]));                        
        let children = [];
        if(model.components){
            let filteredComponents = model.components.filter((item:any) => this.filter(allComponents[item]));             
            for ( let i = 0, l = filteredComponents.length; i < l; i ++ ) {           
                let child =  this.buildComponentsHierachy( "component_"+childId,filteredComponents[ i ] , allComponents);
                children.push(child);   
            }         
        }
        if(model.children){
            let filteredChildren = model.children.filter((item:any) => this.filter(allmodels[item]));   
            for ( let i = 0, l = filteredChildren.length; i < l; i ++ ) {
                let child:any = this.buildChildHierachy("model_"+childId,filteredChildren[i] , allmodels, allComponents);
                children.push(child);   
            }
        }

        let name = model.name || "model_" + childId;   
        let tempModel = { 
            //name,
            id : "model_" + childId,
            pid,
            children,
            customData : model,
            title : name,
            key : "model_" + childId,
            attributes : model.attributes                
        };
        return tempModel;    

    }

    private processGeometries(geometryArray:any[])
    {
        let geometries = [];
        if(geometryArray && geometryArray instanceof Array)
        {  
            for(let i = 0 ; i < geometryArray.length; i++ )
            {
                let geometryIndex = geometryArray[i];
                let geometry = JSON.parse(JSON.stringify(this.mcax.geometries[geometryIndex]));

                if(geometry.representations)
                    geometry.representations = this.processRepresentation(geometry.representations, geometryIndex);

                let name = geometry.name || "geometry_" + geometryIndex;    
                let tempGeometry = {
                    name,
                    id : "geometry_" + geometryIndex,
                    customData : geometry
                };
                geometries.push(tempGeometry);
            }
        }
        return geometries;

    
    }

    private processRepresentation(representationArray:any[], geometryIndex:any){

        let representations = [];
        if(representationArray && representationArray instanceof Array)
        {  
            for(let i = 0 ; i < representationArray.length; i++ )
            {
                let representationIndex = representationArray[i];
                let representation = JSON.parse(JSON.stringify(this.mcax.representations[representationIndex]));
                let name = representation.name || "representation_" + representationIndex;    
                let tempRepresentation = {
                    name,
                    id : "representation_" + representationIndex,
                    geometryIndex : geometryIndex,
                    customData : representation
                };
                representations.push(tempRepresentation);  

            }
        }
        return representations;

    }
  
    private filter(item:any) {
        return item.active;
    }
}

export {ModelTreeBuilder, ModelTree, TreeNode};