import  RequestMeter  from './RequestMeter.js';

export default class NetworkMetrics{

    static URLMap = new Map(); 
    constructor () {
    }

    static addURL(url:any){
        let reqObj = new RequestMeter(url);
        NetworkMetrics.URLMap.set(url, reqObj);
        return reqObj;
    }

    static getRequestObject(url:any){
        return NetworkMetrics.URLMap.get(url);
    } 

    static getAllMetrics(){
        let rtnString = "";
        NetworkMetrics.URLMap.forEach((value) =>{
            if(rtnString === "")
            {
                rtnString = `${value.getMetrics()}`
            }
            else{
            rtnString = 
`${rtnString} 

${value.getMetrics()}`;
            }
        });
    
        return rtnString;
    }

}