enum statusIconType {
    NONE= 0,
    ERROR = 1,
    WARN = 2,
    INFO = 3,
    SUCCESS = 4 
}

class Logger
{
    static externalLogger:any = null;

    static setExternalLogger(externalLogger:any){
        this.externalLogger = externalLogger;
    }

    static setStatusBar(text:string,iconType = statusIconType.INFO)
    {
        if(this.externalLogger){
            this.externalLogger.setStatusBar(text, iconType);
        }
        else{
            console.log(text);
        }            
    }

    static clearStatusBar()
    {
        if(this.externalLogger)
            this.externalLogger.clearStatusBar();
    }
}

export { Logger, statusIconType };