import { Legend } from "./Legend";
import { Utility } from './Utility.js';

class LegendManager
{
    private Legends:Map<any,any>;
    private defaultLegendsID: any;
    constructor(){
        this.Legends = new Map();
        this.defaultLegendsID = null;
    }

    createLegend(){
        let id = Utility.create_UUID();
        let legend = new Legend(id);
        this.Legends.set(id, legend);
        if(this.defaultLegendsID === null)
            this.defaultLegendsID = id;
        return id;
    }

    getLegend(id:any){
        if(id)
            return this.Legends.get(id);
        return this.Legends.get(this.defaultLegendsID);
    }


}
export { LegendManager };