import { SecondsToStructuredString, BytesToStructuredString, SpeedToStructuredString } from './Contants.js';

export default class RequestMeter{
        private _url:any;
        private _requestInitaitedOn:any;
        private _firstResponseReceivedOn:any;
        private _finalResponseReceivedOn:any;
        private _isErrorOccurred:boolean;
        private _isSuccess:boolean;    
        private _loaded:any;
        private _progessList:any[];
        private _error:any;
    
        public size:any;
        public timeForFirstResponse:any;
        public timeForDownload:any;
    constructor (url:any) {     
        this._url  = url,
        this._requestInitaitedOn = null,
        this._firstResponseReceivedOn = null,
        this._finalResponseReceivedOn = null,
        this._isErrorOccurred = false,
        this._isSuccess = false,    
        this._loaded = 0;
        this._progessList = [];
        this._error = null;
       
        this.size = 0;
        this.timeForFirstResponse = null;
        this.timeForDownload = null;
    }

    requestInitaited()
    {
        this._requestInitaitedOn = new Date().getTime();
    }

    addProgress(event:any)
    {
        //console.log("event : ", event);
        if(event){
            let current_time =  new Date().getTime();

            if(this._progessList.length === 0) {
                this._firstResponseReceivedOn = current_time;
                this.timeForFirstResponse = this._firstResponseReceivedOn - this._requestInitaitedOn;
                this.size = event.total;
            }
            if(event.loaded === event.total) {       
                this._finalResponseReceivedOn = current_time;
                this.timeForDownload = this._finalResponseReceivedOn - this._requestInitaitedOn;     
            }

            let prvTime = this._requestInitaitedOn;            
            var bitsLoaded = event.loaded;
            if(this._progessList.length > 0){
                prvTime = this._progessList[this._progessList.length - 1].time;
                bitsLoaded = event.loaded - this._progessList[this._progessList.length - 1].loaded;
            }
                      
            let durationInMS  = (current_time - prvTime);
            let durationInSec = durationInMS / 1000;
            //bitsLoaded = bitsLoaded * 8;
            let speedBps = bitsLoaded / durationInSec;

            let progressEvent = {
                time : current_time,
                loaded : event.loaded,
                duration : durationInMS,
                //event : event,
                speed : speedBps
            }
            this._progessList.push(progressEvent);
            this._loaded = event.loaded;
        }

    }

    succeeded()
    {
        this._isSuccess = true; 
    }

    errorOccurred(errorString:string)
    {
        this._isErrorOccurred = true; 
        this._error =  errorString;
    }

    getTransferSpeed()
    {
        let transferSpeed = {
            maxSpeed : 0,
            avgSpeed : 0,
            minSpeed : 0,
        }   
        for(let i = 1; i < this._progessList.length; i++) //skip the first response since in contains server processing time
        {
            let item = this._progessList[i];  
            if(item.speed < transferSpeed.minSpeed || transferSpeed.minSpeed == 0) 
                transferSpeed.minSpeed = item.speed;

            if(item.speed > transferSpeed.maxSpeed) 
                transferSpeed.maxSpeed = item.speed;
            
            transferSpeed.avgSpeed  = transferSpeed.avgSpeed + item.speed;                          
        }
        if(this._progessList.length > 1)
        transferSpeed.avgSpeed = transferSpeed.avgSpeed /  (this._progessList.length - 1);

        return transferSpeed;
    
    }
 
    getMetrics(){
        let metricsString = "";
       
        if(this._isSuccess === true && this._isErrorOccurred === false ){            
            let transferSpeed = this.getTransferSpeed();

            let size = BytesToStructuredString(this.size);
            let timeForFirstResponse = SecondsToStructuredString(this.timeForFirstResponse);
            let timeToDownload = SecondsToStructuredString(this.timeForDownload);
            let minSpeed = SpeedToStructuredString(transferSpeed.minSpeed);
            let maxSpeed = SpeedToStructuredString(transferSpeed.maxSpeed);
            let avgSpeed = SpeedToStructuredString(transferSpeed.avgSpeed);

        metricsString  =    
`URL :  ${this._url}
Size :  ${size}
Waiting (TTFB) :  ${timeForFirstResponse}
TimeToDownload :  ${timeToDownload}
MIN Speed :  ${minSpeed}
MAX Speed :  ${maxSpeed}
AVG Speed :  ${avgSpeed}
Chunk Count :  ${this._progessList.length}`;
        }
        else if(this._isErrorOccurred === true &&  this._error !== null)
        {
            metricsString  =    
`URL :  ${this._url}
Error :  ${this._error}`;
        }
        else{
            metricsString  =    
`URL :  ${this._url}
Error :  Unknown Error`;

        }

        return  metricsString;

    }
}